import React, {useState} from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import {restoreState} from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)
    const [isTimer, setIsTimer] = useState<boolean>(false)

    const getTime = (date: any): string => {
    

        const hours = String(date?.getHours()).padStart(2, '0');
        const minutes = String(date?.getMinutes()).padStart(2, '0');
        const seconds = String(date?.getSeconds()).padStart(2, '0');
        return `${hours}:${minutes}:${seconds}`;
    }

    const getDate = (date: any): string => {
    
        const day = String(date?.getDate()).padStart(2, '0');
        const month = String(date?.getMonth() + 1).padStart(2, '0');
        const year = date?.getFullYear();
        return `${day}.${month}.${year}`;
    }

    const getDay = (date: any): string => {
    
        return date.toLocaleString('en-US', { weekday: 'long' });
        
    }

    const getMonth = (date: any): string => {
    
        return date.toLocaleString('en-US', { month: 'long' }); 
    }

    const start = () => {

        const setActualDate = () => {
            setDate(new Date())
            
        }
        
        const timerId = window.setInterval(setActualDate, 1000)
        setTimerId(timerId)
        setIsTimer(true)
        
                
        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)

    }

    const stop = () => {
        clearInterval(timerId)
        setTimerId(undefined)
        setIsTimer(false)
        //   //    ,    (timerId <- undefined)

    }

    const onMouseEnter = () => { //   //     
        setShow(true)
    }
    const onMouseLeave = () => { //   //      
        setShow(false)
    }

    const stringTime = getTime(date) || <br/> // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
    const stringDate = getDate(date) || <br/> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const stringDay = getDay(date) || <br/> //  
    const stringMonth = getMonth(date) || <br/> //  

    return (
        <div className={s.clock}>
            <div
                id={'hw9-watch'}
                className={s.watch}
                onMouseEnter={onMouseEnter}
                onMouseLeave={onMouseLeave}
            >
                <span id={'hw9-day'}>{stringDay}</span>,{' '}
                <span id={'hw9-time'}>
                    <strong>{stringTime}</strong>
                </span>
            </div>

            <div id={'hw9-more'}>
                <div className={s.more}>
                    {show ? (
                        <>
                            <span id={'hw9-month'}>{stringMonth}</span>,{' '}
                            <span id={'hw9-date'}>{stringDate}</span>
                        </>
                    ) : (
                        <>
                            <br/>
                        </>
                    )}
                </div>
            </div>

            <div className={s.buttonsContainer}>
                <SuperButton
                    className={s.button}
                    id={'hw9-button-start'}
                    disabled={isTimer} //   //    
                    onClick={start}
                >
                    Start
                </SuperButton>
                <SuperButton
                    className={s.button}
                    id={'hw9-button-stop'}
                    disabled={!isTimer} //   //     
                    onClick={stop}
                >
                    Stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
